* {
  margin: 0;
  padding: 0;
}
h2 {
  font-family: Poppins, "Roboto", sans-serif;
  letter-spacing: 1px;
  font-weight: 500;
  color: #333;
  margin-top: 55px;
  margin-left:400px;
}

.form {
  width: 827px;
  background-color: #fff;
  padding: 20px 30px;
  border-radius: 8px;
  margin-bottom: 30px;
  margin-left:400px;
  margin-top: 30px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.form p {
  color: #777;
  padding-bottom: 8px;
}

.form-group {
  display: flex;
  align-items: flex-start; 
  margin-top: 15px;
}

.form-group label {
  width: 250px;
  font-size: 15px;
  color: #555;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group input[type="tel"],
.form-group input[type="file"],
.form-group textarea {
  flex: 1;
  padding: 10px;
  margin-left: 10px;
  border: 1px solid #caccd0;
  background-color: #fafbfc;
  border-radius: 4px;
  font-size: 13px;
  box-sizing: border-box;
  color: #555;
}

.form-group textarea {
  height: 80px;
  resize: vertical;
  padding: 10px;
  border: 1px solid #caccd0;
}

.form-group input[type="file"] {
  padding: 5px;
  font-size: 12.5px;
  color: #777;
}

.star {
  color: red;
}

.description-message , .input-description-message{
  font-size: 13px;
  color: #777;
  margin: 10px 100px 0 0; 
}


.form-group input::placeholder,
.form-group textarea::placeholder {
  font-size: 12.5px;
  color: #777;
  letter-spacing: 0.5px;
}

button[type="submit"] {
  width: 120px;
  background-color: #4CAF50;
  color: white;
  padding: 12px;
  margin: 15px 0 0 250px;
  border: none;
  border-radius: 18px;
  cursor: pointer;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  background-color: #f5fbff;
  border-color: #6fb4ea;
}

#captcha{
  padding-left: 255px;
}
